import React from 'react';
import '../style/TicketListItem.css'

const subjectTruncate = (subjString) => {
    if (subjString.length > 50) {
        return subjString.slice(0, 45) + '...'
    } else {
        return subjString;
    }
};

const TicketListItem = (props) => {
    return (
        <li className="ticketlistitem">
            <ul className="ticketlistitem-list">
                <li>{subjectTruncate(props.ticket.subject)}</li>
                <li>{props.ticket.status}</li>
                <li>{props.ticket.requester}</li>
                <li>7:15AM</li>
                <li>{props.ticket.group}</li>
            </ul>
        </li>
    );
};

export default TicketListItem