const App = () => {
    const [tickets, setTickets] = useState([]);
    const [overviewStatus, setOverviewStatus] = useState([0, 0, 0, 0])

    const fetchOverviewStatus = (ls) => {
        const status = [0, 0, 0, 0]
        for (let i = 0; i < ls.length; i++) {
            if (ls[i].status === "Unviewed") {status[0]++}
            else if (ls[i].status === "Awaiting Your Reply") {status[1]++}
            else if (ls[i].status === "Awaiting Client Reply") {status[2]++}
            else {status[3]++};
        };
        setOverviewStatus(status);
    };

    const fetchTickets = async () => {
        const response = await firebase.get('.json');
        setTickets(response.data.slice(1,));
        fetchOverviewStatus(response.data.slice(1,));
    };

    useEffect(() => {
        fetchTickets();
    }, []);

    console.log("From App:", tickets);

    return (
        <BrowserRouter>
            <div>
                <Route path="/" exact component={SignIn} />
                <Route 
                    path="/dashboard"
                    render={() => <Dashboard tickets={tickets} overviewStatus={overviewStatus} />}
                />
            </div>
        </BrowserRouter>
        // <div>
        //     <Dashboard tickets={tickets} overviewStatus={overviewStatus} />
        // </div>
    );
};

export default App;